<template>
  <div id="add-blog">
    <h2></h2>
    <form v-if="!submited">
      <label></label>
      <input type="text" v-model="blog.title" required />

      <label></label>
      <textarea v-model="blog.content"></textarea>

      <div id="checkboxes">
        <label>Vue.js</label>
        <input type="checkbox" value="Vue.js" v-model="blog.categories" />
        <label>Node.js</label>
        <input type="checkbox" value="Node.js" v-model="blog.categories" />
        <label>React.js</label>
        <input type="checkbox" value="React.js" v-model="blog.categories" />
        <label>Angular4</label>
        <input type="checkbox" value="Angular4" v-model="blog.categories" />
      </div>
      <label></label>
      <select v-model="blog.author">
        <option v-for="author in authors">{{ author }}</option>
      </select>
      <button @click.prevent="post"></button>
    </form>

    <div v-if="submited">
      <h3></h3>
    </div>

    <div id="preview">
      <h3></h3>
      <p>:{{ blog.title }}</p>
      <p></p>
      <p>{{ blog.content }}</p>
      <p></p>
      <ul>
        <li v-for="category in blog.categories">{{ category }}</li>
      </ul>
      <p>:{{ blog.author }}</p>
    </div>
  </div>
</template>

<script>
export default {
  // https://jsonplaceholder.typicode.com/
  // https://jsonplaceholder.typicode.com/posts
  name: "add-blog",
  data() {
    return {
      blog: {
        title: "",
        content: "",
        categories: [],
        author: "",
      },
      authors: ["Black", "Jack", "Joe"],
      submited: false,
    };
  },
  methods: {
    post() {
      this.$http
.post("https://myblog-667d9-default-rtdb.firebaseio.com/posts.json", this.blog)
        .then(function (data) {
          console.log(data);
          this.submited = true;
        });
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#add-blog * {
  box-sizing: border-box;
}

#add-blog {
  margin: 20px auto;
  max-width: 600px;
  padding: 20px;
}

label {
  display: blodk;
  margin: 20px 0 10px;
}

input[type="text"],
textarea,
select {
  display: block;
  width: 100%;
  padding: 8px;
}

textarea{
  height: 200px;
}

#checkboxes label {
  display: inline-block;
  margin-top: 0;
}

#checkboxes input {
  display: inline-block;
  margin-right: 10px;
}

button {
  display: block;
  margin: 20px 0;
  background: crimson;
  color: #fff;
  border: 0;
  padding: 14px;
  border-radius: 4px;
  font-size: 18px;
  cursor:pointer
}

#preview{
  padding: 10px 20px;
  border: 1px dotted #ccc;
  margin: 30px 0;
}

h3{
  margin-top: 10px;
}
</style>



